<template>
  <form @submit.prevent="checkForm">
    <div class="string">
      <span>E-mail:</span>
      <input id="email" type="email" v-model="form.email" placeholder="ivanov@mail.ru">
         <p v-if=" !$v.form.email.required && $v.form.email.$dirty" class="error-feedback">
          Обязательное поле
        </p>
        <!--Предупреждение о неправильном вводе-->
        <p v-if=" !$v.form.email.email && $v.form.email.$dirty" class="error-feedback">
          Некорректный email
        </p>
    </div>
    <div class="string">
      <span>Пароль:</span>
      <input id="password" type="password" v-model="form.password" placeholder="Введите свой пароль">
    <p v-if= "!$v.form.password.required && $v.form.password.$dirty" class="error-feedback">
      Обязательное поле
    </p>
    <p v-if="  !$v.form.password.minLength && $v.form.password.$dirty" class="error-feedback">
      Здесь должно быть больше 7-и символов
    </p>
    </div>
    <button class="btn glow-on-hover" type="submit">Войти</button>
  </form>
</template>

<script>
  import { validationMixin } from 'vuelidate' 
  import { required, minLength, email} from 'vuelidate/lib/validators'
import sha256 from '../api/crypt.js'
export default {
  name: 'loginForm',
  mixins: [validationMixin],
  validations: { 
    form: {
      password: { required, minLength: minLength(7) },
      email:{required, email}
    }
  },
  data() {
    return {
      form: {
        email: '',
        password: '',
      }
    }
  },
  methods: {
    async checkForm() {
      this.$v.form.$touch()
      if (!this.$v.form.$error) {
        try {
          window.localStorage.setItem('getTime', false)
          window.localStorage.setItem('email', this.form.email)
          const encoded_password = sha256(this.form.password) // ultimate protect
          const payload = JSON.stringify({
            "email": this.form.email,
            "token": encoded_password
          });
          const response = await this.$api.auth.login(payload);
          console.log(response)
          if(response.data.message == 'Logged in successfully') // if ok
          {
            window.localStorage.setItem('authFlag', true); //  похуй
            window.localStorage.setItem('userId', response.data.id_user); 
            this.$router.push({ name: 'personalCabinet' })
          }else{
            alert('Неверное имя пользователя или пароль, попробуйте ещё раз!')
          }
        } catch (e) {
          console.log(e)
        }
      }
    }
  }
}
</script>
  
  


