<template>
  <form @submit.prevent="checkForm">
    <div class="string">
      <span>ФИО:</span>
      <input id="name" type="name" v-model.trim="form.name" placeholder="Иванов Иван Иванович">
      <p v-if="!$v.form.name.required && $v.form.name.$dirty" class="error-feedback">
        Обязательное поле
      </p>

    </div>

    <div class="string">
      <span>E-mail:</span>
      <input id="email" type="email" v-model="form.email" placeholder="ivanov@mail.ru">
      <p v-if="!this.$v.form.email.required && $v.form.name.$dirty" class="error-feedback">
        Обязательное поле
      </p>
      <!--Предупреждение о неправильном вводе-->
      <p v-if="!this.$v.form.email.email && $v.form.name.$dirty" class="error-feedback">
        Некорректный email
      </p>
    </div>



    <div class="string">
      <span>Школа:</span>
      <input id="school" type="text" v-model="form.school" placeholder="1234">
      <p v-if="!this.$v.form.school.required && $v.form.name.$dirty" class="error-feedback">
        Обязательное поле
      </p>
    </div>



    <div class="string">
      <span>Класс:</span>
      <input id="class" type="text" v-model="form.class" placeholder="11А">
      <p v-if="!this.$v.form.class.required && $v.form.name.$dirty" class="error-feedback">
        Обязательное поле
      </p>
    </div>


    <div class="string">
      <span>Пароль:</span>
      <input id="password" type="password" v-model="form.password" placeholder="Введите пароль">
      <p v-if="!this.$v.form.password.required && $v.form.name.$dirty" class="error-feedback">
        Обязательное поле
      </p>
      <p v-if="!this.$v.form.password.minLength && $v.form.name.$dirty" class="error-feedback">
        Здесь должно быть больше 7-и символов
      </p>
    </div>
    <button class="btn glow-on-hover" type="submit">Зарегистрироваться</button>
  </form>
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, minLength, email } from 'vuelidate/lib/validators'
import sha256 from '../api/crypt.js'
export default {
  name: 'registerForm',
  mixins: [validationMixin],
  data() {
    return {
      form: {
        name: '',
        email: '',
        school: '',
        class: '',
        password: '',
      }
    }
  },
  validations: {
    form: {
      name: { required },
      password: { required, minLength: minLength(7) },
      email: { required, email },
      school: { required },
      class: { required }
    }
  },
  methods: {
    async checkForm() {
      this.$v.form.$touch()
      if (!this.$v.form.$error) {
        try {
          const encoded_password = sha256(this.form.password) // ultimate protect
          const payload = JSON.stringify({
            name: this.form.name,
            email: this.form.email,
            school: this.form.school,
            class: this.form.class,
            password: encoded_password,
          });
          const response = await this.$api.auth.register(payload)
          if (response.data.status == 'success') {
            window.localStorage.setItem('email', this.form.email)
            this.$router.push({ name: 'login' })
          } else if (response.data.status == 'email is busy') {
            alert('Этот адрес электронной почты занят используйте другой!')
          }
        } catch (e) {
          console.log(e)
        }
      }
    }
  }
}
</script>
  
  
<style scoped>
.btn {
  margin-top: 10px;
  margin-right: 6vw;
  border: none;
  background-color: #373636;
  color: #FFFFFF;
  border-radius: 5px;
  cursor: pointer;
}

.margin-left {
  margin-left: 5px;
}

.title-end {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.padding {
  padding-bottom: 10px;
}

.media-auth {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

input {
  background: #F2F2F2;
  border: none;
  width: 300px;
  border-radius: 4px;
  padding-left: 4px;
  margin-left: 5px;
}

.string {
  margin: 5px;
}

span {
  font-family: 'IBM Plex Serif';
  font-style: normal;
  font-weight: 400;
  color: #696969;
}

form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-bottom: 30px;
}

.back-container {
  position: absolute;
  width: 1512px;
  height: 982px;
  left: 0px;
  top: 0px;
}

.title-p {
  margin: 20px;
  padding-top: 20px;
  font-family: 'IBM Plex Serif';
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 26px;
  align-items: center;
  text-align: center;

  color: #373636;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 120px;
  width: 440px;
  height: 440px;
  background: #FFFFFF;
  border-radius: 5px;
}

.error-feedback {
  font-size: 12px;
  color: red;
}

@media screen and (max-width:450px) {
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
    width: 300px;
    height: 440px;
    background: #FFFFFF;
    border-radius: 5px;
  }

  input {
    background: #F2F2F2;
    border: none;
    width: 180px;
    font-size: 10px;
    border-radius: 4px;
    padding-left: 4px;
    margin-left: 5px;
  }
}
.glow-on-hover {
    border: none;
    outline: none;
    color: #fff;
    background: #111;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
}

.glow-on-hover:before {
    content: '';
    background: linear-gradient(45deg, #4f4f4f);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.glow-on-hover:active {
    color: rgb(113, 113, 113)
}

.glow-on-hover:active:after {
    background: transparent;
}

.glow-on-hover:hover:before {
    opacity: 1;
}

.glow-on-hover:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgb(121, 121, 121);
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}
</style>
